#root{width:100vw;height:100vh}

body,html{margin:0;width:100vw;height:100vh;font-family:"IBM Plex Sans";overflow-x:hidden;background-image:url(/linucks.io-client/static/media/bg2.8513e602.jpg);color:#fafafa;text-align:center}.text-white{color:white}

.btn{background-color:#97015B;border-color:#97015B;margin-top:0.8rem;font-weight:bold}.btn:hover{background-color:#730FC3}.logo{height:2rem;margin-top:0.8rem}.carousel{height:15vh}.navbar{position:-webkit-sticky;position:sticky;top:0px;z-index:10;-webkit-backdrop-filter:blur(20px);backdrop-filter:blur(20px);border-bottom:#ffffff;border-width:2px}.navbar.colorChange{background-color:#000}.hero{background-image:url(/linucks.io-client/static/media/bg.a1e9d8d2.png)}.header{font-weight:bold;font-size:64px}.subheader{color:#fafafacc;font-size:20px}.paragraph{color:#fafafab0;font-size:16px}.reset{color:white}

.alphabets>*{margin-right:5px;color:white}

.spacer{height:8vh}

.alphabet-card{width:20vw;height:30vh;margin-bottom:2rem}.card-title{color:black}.distro-names{color:black}.alphabet-card-body{overflow-y:auto}::-webkit-scrollbar{width:10px}::-webkit-scrollbar-track{border-radius:10px}::-webkit-scrollbar-thumb{background:#97015B;border-radius:10px}

.cons{color:#97015B}.pros{color:#97015B}

